        }
        nav ul li {
            display: block;
            border-bottom: 1px solid #ccc;
            text-align: center;
        }
        nav ul li:last-child {
            border: none
        }
        nav a {
            font-size: 100%;
            position: relative;
            right: 9%;
        }
        #logo-holder {
            width: 50%;
            left: 25%;
            .logo {
                width: 100%;
            }
        }
        #links {
            width: 90%;
            left: 0;
            margin-top: 8%;
            #left,
            #right {
                text-align: center;
            }
            .media-links {
                width: 35%;
                margin-top: 5%;
            }
            h1 {
                display: none;
            }
        }
    }
}


