            }
        }
        #links {
            width: 80%;
            right: 0;
            left: 10%;
            text-align: center;
        }
        #left,
        #right {
            width: 50%;
            text-align: center;
            .media-links {
                width: 40%;
            }
            h1 {
                display: none;
            }
        }
    }
}